import { useFlags } from "launchdarkly-react-client-sdk"
import { getlocalOverrideFlags } from "@rp/react/web/common/hooks"
import { useFeatureAtom } from "../atoms/feature-flags"

const useDisabledFeatures = () => {
  const [SHOW_SEARCH] = useFeatureAtom("search")
  const [SHOW_CHAT] = useFeatureAtom("chat")
  const [SHOW_FILES] = useFeatureAtom("file")
  const [SHOW_WATCHLISTS] = useFeatureAtom("watchlist")
  const [SHOW_BRIEF] = useFeatureAtom("brief")
  const [SHOW_AGENT] = useFeatureAtom("enableAgentMode")
  const [SHOW_SOURCE_FILES] = useFeatureAtom("enableSourceFiles")
  const [SHOW_SOURCE_EARNINGS_CALLS] = useFeatureAtom("enableSourceEarningsCalls")
  const [SHOW_SOURCE_REGULATORY_FILINGS] = useFeatureAtom("enableSourceRegulatoryFilings")
  const [SHOW_SOURCE_MARKET_INTELLIGENCE] = useFeatureAtom("enableSourceMarketIntelligence")
  const [SHOW_SOURCE_FACTSET_TRANSCRIPTS] = useFeatureAtom("enableSourceFactsetTranscripts")
  const [SHOW_SOURCE_QUARTR_PRO] = useFeatureAtom("enableSourceQuartrPro")
  const [SHOW_SOURCE_PRICING_CULTURE] = useFeatureAtom("enableSourcePricingCulture")
  const [SHOW_SOURCE_CRYPTO_MARKETS] = useFeatureAtom("enableSourceCryptoMarkets")
  const [SHOW_SOURCE_MIDDLE_EAST_MARKETS] = useFeatureAtom("enableSourceMiddleEastMarkets")
  const [SHOW_SOURCE_FINANCIAL_TIMES] = useFeatureAtom("enableSourceFinancialTimes")
  const [SHOW_SOURCE_EXPERT_NETWORKS] = useFeatureAtom("enableSourceExpertNetworks")

  const { webFeEnableEngineAgents } = useFlags()
  const enabledEngineAgents = getlocalOverrideFlags(
    "webFeEnableEngineAgents",
    webFeEnableEngineAgents,
  )

  return {
    flagsStatus: {
      search: SHOW_SEARCH,
      chat: SHOW_CHAT,
      "file:index": SHOW_FILES,
      watchlist: SHOW_WATCHLISTS,
      "extra:brief": SHOW_BRIEF,
      agent: SHOW_AGENT,
    },
    sourceFilters: {
      news: true,
      earnings_calls: SHOW_SOURCE_EARNINGS_CALLS,
      filings: SHOW_SOURCE_REGULATORY_FILINGS,
      files: SHOW_SOURCE_FILES,
      "job-analytics": enabledEngineAgents,
      price: enabledEngineAgents,
      "sentiment-attention": enabledEngineAgents,
      estimates: enabledEngineAgents,
      crunchbase: enabledEngineAgents,
      "knowledge-graph": enabledEngineAgents,
      "etf-holdings": enabledEngineAgents,
      market_intelligence: SHOW_SOURCE_MARKET_INTELLIGENCE,
      transcripts: SHOW_SOURCE_FACTSET_TRANSCRIPTS,
      quartr_pro: SHOW_SOURCE_QUARTR_PRO,
      market_narratives: SHOW_SOURCE_PRICING_CULTURE,
      middle_eastern_markets: SHOW_SOURCE_MIDDLE_EAST_MARKETS,
      crypto_markets: SHOW_SOURCE_CRYPTO_MARKETS,
      financial_times: SHOW_SOURCE_FINANCIAL_TIMES,
      expert_networks: SHOW_SOURCE_EXPERT_NETWORKS,
    },
  }
}

export { useDisabledFeatures }
